        line-height: var(--bnm-header-line-height);
        letter-spacing: var(--bnm-header-letter-spacing);
        border-bottom: 2px solid #000000;
        margin: 0;
        padding: 0;
	}

    article {
        .entry-title {
            font-size: var(--bnm-title-font-size);
            letter-spacing: var(--bnm-title-letter-spacing);
            line-height: var(--bnm-title-line-height);
            margin: var(--bnm-title-margin);
            padding: var(--bnm-title-padding);
            
            a {
                color: var(--bnm-title-color);
                position: relative;
                text-decoration: none;
                z-index: 2;
    
                &:hover {
                    color: var(--bnm-title-hover-color);
                }
            }
        }

        .entry-meta {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            color: var(--bnm-meta-color);
            font-size: var(--bnm-meta-font-size);
            padding: var(--bnm-meta-padding);
            margin: var(--bnm-meta-margin);
            letter-spacing: var(--bnm-meta-letter-spacing);
            line-height: var(--bnm-meta-line-height);
            text-decoration: inherit;

            a {
                color: var(--bnm-meta-color);
                text-decoration: inherit;
                font-size: var(--bnm-meta-font-size);
                line-height: var(--bnm-meta-line-height);
                letter-spacing: var(--bnm-meta-letter-spacing);
                position: relative;
                z-index: 2;

                &:visited {
                    color: var(--bnm-meta-color);
                }
    
                &:hover {
                    color: var(--bnm-meta-hover-color);
                }
            }
        }

        .bnm-category-list {

            font-size: var(--bnm-category-font-size);

            a {
                color: var(--bnm-category-color);
                margin: var(--bnm-category-margin);
                text-decoration: none;
    
                &:hover {
                    color: var(--bnm-category-hover-color);
                }
            }
        }
    }

    .byline,
    .posted-on,
    .bnm-comment-count {
        
        margin-right: var(--bnm-meta-spacing);

        a {
            text-decoration: none;
            position: relative;
			      z-index: 2;
            color: var(--bnm-meta-color);

            &:hover {
                color: var(--bnm-meta-hover-color);
            }
        }
    }

    .bnm-category-list {
      display: flex;
      flex-wrap: wrap;

      a {
        display: inline-block;
        position: relative;
        z-index: 2;
        font-weight: bold;
      }
    }

	&.bnm-box-cat {
		article .bnm-category-list {
			a {
				background-color: var(--bnm-category-bg-color);
				padding: var(--bnm-category-padding);

				&:hover {
					background-color: var(--bnm-category-bg-hover-color);
				}
			}
		}
	}

    .post-thumbnail {
      margin: 0;
      height: 100%;
      width: 100%;
      padding: 0;
      position: relative;

      a,
      img {
        display: block;
        height: 100%;
        object-fit: cover;
        width: 100%;

        &.image-fit-contain {
          object-fit: contain;
        }
      }
	}

  .bnm-slider-content {
		bottom: 0;
		background: linear-gradient(0deg,rgba(0,0,0,0.7),rgba(0,0,0,0) 100%,rgba(0,0,0,0));
		color: var(--bnm-meta-color);
		left: 0;
		padding: 1.5em;
		position: absolute;
		right: 0;
	}

  &.bnm-sw-style-3 {
    .bnm-slider-content {
      position: relative;
      background: transparent;
    }

    .post-thumbnail {
      height: auto;
    }
  }

	&.hide-pagination {
		.bnm-swiper-pagination {
			display: none;
		}
	}

	&.hide-next-prev-btns {
		.bnm-swiper-button-prev,
		.bnm-swiper-button-next {
			display: none;
		}
	}

	.bnm-img-placeholder {
		height: 420px;
		background: #f8f8f8;
	}

    .swiper-slide {
		height: auto;
		max-height: 100vh;
	}

  .swiper-pagination-bullet {
      background-color: #cccccc;
  }

  .swiper-pagination-bullet-active {
      background-color: #ffffff;
  }

}

.updated:not(.published) {
	display: none;
}

.wpbnmposw .swiper-slide:hover .bnm-slider-content {
	background: linear-gradient(0deg,rgba(0,0,0,0.8),rgba(0,0,0,0) 100%,rgba(0,0,0,0));
}

.bnmslovrlay.inside-gut-editor {
	cursor: pointer
}

.bnm-swiper-button-next, 
.bnm-swiper-button-prev {
  color: #ffffff !important;
  width: 45px;
  height: 45px;
  background-color: rgba(0, 0, 0, 0.5);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 24px 24px;
  border-radius: 5px;

  svg {
    font-size: 20px !important;
    width: auto;
    height: auto;
  }

  &::before,
  &::after {
    display: none;
  }
}

.bnm-swiper-button-prev {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 640 640'%3E%3Cpath d='M169.4 297.4C156.9 309.9 156.9 330.2 169.4 342.7L361.4 534.7C373.9 547.2 394.2 547.2 406.7 534.7C419.2 522.2 419.2 501.9 406.7 489.4L237.3 320L406.6 150.6C419.1 138.1 419.1 117.8 406.6 105.3C394.1 92.8 373.8 92.8 361.3 105.3L169.3 297.3z' fill='%23fff'/%3E%3C/svg%3E");
}

.bnm-swiper-button-next {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 640 640'%3E%3Cpath d='M471.1 297.4C483.6 309.9 483.6 330.2 471.1 342.7L279.1 534.7C266.6 547.2 246.3 547.2 233.8 534.7C221.3 522.2 221.3 501.9 233.8 489.4L403.2 320L233.9 150.6C221.4 138.1 221.4 117.8 233.9 105.3C246.4 92.8 266.7 92.8 279.2 105.3L471.2 297.3z' fill='%23fff'/%3E%3C/svg%3E");
}

.swiper-horizontal {
  .bnm-swiper-button-prev,
  & ~ .bnm-swiper-button-prev,
  &.swiper-rtl .bnm-swiper-button-next,
  &.swiper-rtl ~ .bnm-swiper-button-next {
    left: 27px;
    right: auto;
  }
  .bnm-swiper-button-next,
  & ~ .bnm-swiper-button-next,
  &.swiper-rtl .bnm-swiper-button-prev,
  &.swiper-rtl ~ .bnm-swiper-button-prev {
    right: 27px;
    left: auto;
  }
}

.bnm-sw-style-2,
.bnm-sw-style-3 {
  @media( min-width: 992px ) {
    .bnm-swiper-button-next, 
    .swiper-rtl .bnm-swiper-button-prev {
      right: -35px;
    }
    .bnm-swiper-button-prev, 
    .swiper-rtl .bnm-swiper-button-next {
      left: -35px;
    }
  }
  .bnm-swiper-button-prev, 
  .bnm-swiper-button-next {
    width: 30px;
    height: 30px;
    background-size: 20px 20px;
    border-radius: 15px;
  }
}

// Style 3
.wpbnmposw.bnm-sw-style-3 .swiper-slide:hover .bnm-slider-content {
	background: transparent;
}

.bnm-sw-style-3 .swiper-wrapper {
  align-items: stretch;
}

.bnm-sw-style-3 .swiper-wrapper .swiper-slide {
  height: auto;
  background: #f5f5f5;
}

.bnm-sw-style-2,
.bnm-sw-style-3 {
  margin-bottom: 40px !important;

  .swiper-horizontal > .swiper-pagination-bullets, 
  .swiper-pagination-bullets.swiper-pagination-horizontal, 
  .swiper-pagination-custom, .swiper-pagination-fraction {
      bottom: -30px;
  }

  .swiper-pagination-bullet {
      background-color: #666;
  }

  .swiper-pagination-bullet-active {
      background-color: #222;
  }

  .bnm-img-placeholder {
    height: auto;
  }
}

// Style 4
.swiper-thumbs {
  .swiper-slide-thumb-active {
    /* Styles for active thumb slide */
  }
}

.bnm-thumbnail-swiper.swiper {
  padding: 10px;
  background: #ddd;
  .swiper-slide {
    width: calc(100% / var(--bnm-thumbs-per-view));
    margin-right: 10px;

    .bnm-swiper-thumb-bg {
      background: #000;
    }
    
    img {
      vertical-align: middle;
      opacity: 0.9;
      box-sizing: border-box;

      &:hover {
        opacity: 1;
        cursor: pointer;
      }
    }

    &.swiper-slide-thumb-active {
      img {
        opacity: 1;
        border: 2px solid #fff;
      }
    }
  }

  .bnm-img-thumb-placeholder {
    height: 100%;
    background: #ffffff;
  }
}

